.container {
  width: 100%;
  height: 80px;
  background-color: rgb(232, 155, 11);
  display: flex;
  align-items: end;
  padding-bottom: 20px;
}

.brandname {
  color: rgb(104, 101, 101);
  font-family: "Montserrat", sans-serif;
  font-size: 40px;
  font-weight: 600;
}

.link_list {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 0px 20px 0px 0px;
}
.page_links {
  display: flex;
  list-style-type: none;
  gap: 15px;
  margin: 0px;
  padding-top: 10px;
  font-family: "Montserrat", sans-serif;
}
.app_links {
  display: flex;
  gap: 15px;
  list-style-type: none;
  margin: 0px;
  padding-top: 10px;
}
.link {
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  font-weight: 600;
  color: rgb(104, 101, 101);
  border: none;
  border-radius: 4px;
  cursor: pointer;
  padding: 8px 16px;
  outline: none;
  transition: background-color 0.4s ease;
}

.link:hover {
  background-color: rgb(245, 172, 83);
}

.home_image {
  width: 50px;
  height: 60px;
  padding: 0px 10px 0px 10px;
}
